/**
 * Heading with Icon block.
 *
 * @package
 */

import { getIconComponent } from './icons-map';

/**
 * Internal dependencies.
 */
import Edit from './edit';

/**
 * WordPress Dependencies.
 */
import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import { RichText } from '@wordpress/block-editor';

/**
 * Register block type.
 */
registerBlockType( 'aigpt3/ai-generated-block', {
	/**
	 * Block title.
	 *
	 * @type {string}
	 */
	title: __( 'AI Generated Contents', 'aquila' ),

	/**
	 * Block icon.
	 *
	 * @type {string}
	 */
	icon: 'welcome-write-blog',

	/**
	 * Block description.
	 *
	 * @type {string}
	 */
	description: __( 'Add AI generated text contents on this block.', 'aquila' ),

	/**
	 * Block category.
	 *
	 * @type {string}
	 */
	category: 'common',

	/**
	 * Attributes.
	 */
	attributes: {
		option: {
			type: 'string',
			default: 'dos',
		},
		content: {
			type: 'string',
			source: 'html',
			selector: 'p',
			default: '', // __( 'Content:', 'aquila' ),
		},
	},

	edit: Edit,

	/**
	 * Save function.
	 *
	 * @param {Object} props Props
	 *
	 * @return {Object} Content.
	 */
	save( props ) {
		const {
			attributes: { option, content },
		} = props;
		const HeadingIcon = getIconComponent( option );
		
		// <span className="aquila-icon-heading__heading d-none">
		// 	<HeadingIcon />
		// </span>

		return (
			<div className="ai-generated-contents">
				{ /* Saves <h2>Content added in the editor...</h2> to the database for frontend display */ }
				<RichText.Content tagName="p" value={ content } />
			</div>
		);
	},
} );
